/* Signup Container */
.signup-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #e9f5ff; /* Light blue background for a softer look */
  padding: 20px; /* Padding to prevent content from touching the screen edges */
}

/* Signup Form */
.signup-form {
  background-color: #ffffff;
  padding: 40px;
  border-radius: 10px; /* Slightly more rounded corners */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1); /* Softer shadow */
  max-width: 450px; /* Slightly wider form */
  width: 100%;
  animation: fadeIn 0.5s ease; /* Subtle fade-in animation */
}

/* Fade-in Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Heading */
.signup-form h2 {
  margin-bottom: 20px;
  font-size: 26px;
  color: #333333;
  text-align: center;
  font-weight: bold;
  letter-spacing: 1px; /* Added letter-spacing for a more modern look */
}

/* Form Group */
.form-group {
  margin-bottom: 25px; /* Increased space between form elements */
}

/* Input Fields */
.form-group input[type="text"],
.form-group input[type="email"],
.form-group input[type="password"],
.form-group input[type="file"] {
  width: 100%;
  padding: 12px; /* Increased padding for larger input fields */
  border: 1px solid #cccccc;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 16px;
  color: #555555;
  transition: all 0.3s ease; /* Smooth transition on focus */
}

/* Input Focus State */
.form-group input:focus {
  outline: none;
  border-color: #0073e6;
  box-shadow: 0 0 0 3px rgba(0, 115, 230, 0.2);
}

/* File Input Label */
.file-label {
  display: block;
  margin-top: 5px;
  font-size: 14px;
  color: #777777;
}

/* Submit Button */
.submit-btn {
  width: 100%;
  padding: 15px; /* Larger button for better usability */
  background-color: #0073e6;
  color: #ffffff;
  font-size: 18px;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease; /* Smooth transition and subtle button press effect */
}

.submit-btn:hover {
  background-color: #005bb5;
  transform: translateY(-2px); /* Subtle lift effect on hover */
}

.submit-btn:active {
  transform: translateY(0); /* Button returns to normal position on click */
}

/* Responsive Design */
@media (max-width: 600px) {
  .signup-form {
    padding: 25px; /* Reduced padding for smaller screens */
  }

  .signup-form h2 {
    font-size: 22px;
  }

  .form-group input {
    padding: 10px; /* Slightly smaller input fields for mobile */
  }

  .submit-btn {
    padding: 12px;
    font-size: 16px;
  }
}




/* Success Container */
.success-container {
  text-align: center;
  padding: 60px;
  background-color: #e9f5ff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 50px auto;
  max-width: 600px;
  animation: fadeIn 0.5s ease; /* Subtle fade-in animation */
}

/* Heading */
.success-container h1 {
  color: #28a745;
  font-size: 28px;
  margin-bottom: 20px;
  font-weight: bold;
}

/* Paragraph */
.success-container p {
  font-size: 18px;
  color: #555555;
  margin-bottom: 30px;
  line-height: 1.5;
}

/* Button */
.success-container button {
  padding: 12px 30px;
  border: none;
  background-color: #28a745;
  color: #ffffff;
  cursor: pointer;
  border-radius: 5px;
  font-size: 18px;
  font-weight: bold;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.success-container button:hover {
  background-color: #218838;
  transform: translateY(-2px); /* Subtle lift effect on hover */
}

.success-container button:active {
  transform: translateY(0); /* Button returns to normal position on click */
}

/* Fade-in Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


  